export default class ButtonController {
	constructor($timeout) {
		'ngInject';
		this.disabled = false;
		this.$timeout = $timeout;
		this.tiggleDisabled = true;
	}

	sayHello(btn) {
		btn.state = {loading: true};
		this.tiggleDisabled = false;
		this.$timeout(() => {
			btn.state = {disabled: false, loading: false};
		}, 3000);
	}
}


